.title{
    text-align: center;
    margin-bottom: 4rem;
}

.title h2{
    font-size: 2.5rem;
    font-weight: 300;
    line-height: 1.4;
    text-transform: uppercase;
    color: var(--dark-text);
    margin: 1.5rem 0;
}

.title p2{
    width: 50%;
    min-width: 40rem;
    margin: 0 auto;
}

.features-content{
    display: flex;
    justify-content: center;
}

.features-left{
    flex: 1;
    text-align: center;
}

.features-left img{
    width: 65%;
}

.features-right{
    flex: 1;
}

@media screen and (max-width: 600px) {
    .features-content{
        flex-direction: column;
    }
}