footer#footer{
    background: var(--dark-blue);
    color: #fff;
    width: 100%;
    padding: 8rem 0;

}

.footer {
    display: flex;
    margin: 9px 9px 
}

.footer h4{
    font-size: 1.6rem;
    font-weight: 500;
    line-height: 1.4;
    text-transform: uppercase;
    margin-bottom: 1rem;
}

.footer-links{
    display: flex;
    flex-direction: column;
}

a{
    font-size: 1.2rem;
}

.footer-contact p{
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}