a{
    font-size: 1.6rem;
    font-weight: 300;
    color: var(--color-white);
    text-decoration: none;
    margin-right: 1rem;
}

.button-em{
    display: block;
    text-decoration: none;
    font-size: 2rem;
    font-weight: 600;
    padding: 1.2rem 1.2rem;
    border-radius: 9px;
    border: none;
    cursor: pointer;
    background-color: #ff5823;
    color: #fdf2e9;
    align-self: end;

}

